import { useEffect, useRef } from "react";
import * as VKID from "@vkid/sdk";
import { useAppDispatch } from "../../hooks/reduxHooks"; // Для отправки данных в Redux
import { setUser } from "../../store/userStore"; // Для обновления данных пользователя

const APP_NAME = "vk shuffler"; // Замените на ваше имя приложения

export default function VKIDFloatingOneTap() {
 const containerRef = useRef<HTMLDivElement>(null);
 const dispatch = useAppDispatch(); // Для отправки данных в Redux

 useEffect(() => {
  if (!containerRef.current) return;

  // Рендерим VKID FloatingOneTap
  const floatingOneTap = new VKID.FloatingOneTap();

  floatingOneTap
   .render({
    appName: APP_NAME,
    scheme: VKID.Scheme.DARK,
    lang: VKID.Languages.RUS,
   })
   .on(VKID.FloatingOneTapInternalEvents.LOGIN_SUCCESS, function (payload: any) {
    const code = payload.code
    const deviceId = payload.device_id

    VKID.Auth.exchangeCode(code, deviceId)
    .then((result) => {
     dispatch(setUser({
      access_key: result.access_token
     }))
    })
    .catch(() => {
     console.log('error')
    });
   })
   .on(VKID.WidgetEvents.ERROR, (error: any) => {
    console.error("VKID error:", error);
   });

  // После того, как виджет загружен, мы запускаем авторизацию через VKID.Auth.login()
  //@ts-ignore

  // Очистка при размонтировании компонента
  //@ts-ignore
  return () => floatingOneTap.destroy();
 }, [dispatch]);

 return <div ref={containerRef} />;
}
